<template>
	<div>
		<div class="btn">
			<el-button type="primary" icon="el-icon-delete" @click="delSelection()" :disabled="disabledDel">全部删除</el-button>
			<el-button type="primary" @click="dialogDoc = true">添加文档</el-button>
		</div>
		<el-table :data="tableDate" ref="multipleTable" @selection-change="handleSelectionChange" class="aip-table">
			<el-table-column label="名称" type="selection" align="center"></el-table-column>
			<el-table-column prop="id" label="文档ID" align="center"></el-table-column>
			<el-table-column prop="title" label="文档标题" align="center"></el-table-column>
			<el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
			<el-table-column label="操作" align="center">
				<template slot-scope="scope">
					<el-button size="mini" @click="handleDelete(scope.$index, scope.row)">编辑</el-button>
					<el-button type="danger" size="mini" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>
		<div class="block">
			<el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper" :total="totalPage">
			</el-pagination>
		</div>

		<el-dialog :visible.sync="dialogDoc" title="文档编辑">
			<div>
				<div style="margin: 0 30px">
					<h3 style="height: 40px; line-height: 40px;">文档标题</h3>
					<input type="text" v-model="form.title" style="width: 100%; height: 30px; line-height: 30px;font-size: 15px;">
					<h3 style="height: 40px; line-height: 40px;">文档内容</h3>
					<quill-editor ref="myQuillEditor" v-model="form.detail" :options="editorOption" style="height:500px;margin:0 0 30px 0;"></quill-editor>
					<el-upload class="avatar-uploader quill-img" action="https://api.fengniaotuangou.cn/api/upload" :before-upload="beforeUpload"
					 :on-success='quillImgSuccess' style="display: none">
						<el-button size="small" type="primary" id="imgInput" element-loading-text="插入中,请稍候">点击上传</el-button>
					</el-upload>
					<el-button class="editor-btn" type="primary" @click="submit">提交</el-button>
				</div>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import API from '@/api/modules/document.js'
	import {
		quillEditor
	} from 'vue-quill-editor';
	import 'quill/dist/quill.core.css';
	import 'quill/dist/quill.snow.css';
	import 'quill/dist/quill.bubble.css';
	let  loading;
	export default {
		components: {
			quillEditor
		},
		data() {
			return {
				dialogDoc: false,
				tableDate: [],
				typeList: [],
				form: {
					title: '',
					detail: ''
				},
				disabled: false,
				title: '',
				editorOption: {
					placeholder: '请输入文档内容',
					theme: 'snow',
					modules: {
						toolbar: {
							container: [
								['bold', 'italic', 'underline', 'strike'],
								['blockquote', 'code-block'],
								[{
									'direction': 'rtl'
								}],
								[{
									'size': ['small', false, 'large', 'huge']
								}],
								[{
									'header': [1, 2, 3, 4, 5, 6, false]
								}],
								[{
									'color': []
								}, {
									'background': []
								}],
								[{
									'font': []
								}],
								[{
									'align': []
								}],
								['clean'],
								['link', 'image']
							],  
							handlers: {
                        'image': function (value) {
                          if (value) {
                            document.querySelector('.quill-img input').click()
                          } else {
                            this.quill.format('image', false);
                          }
                        }
                      }
						}
					}
				},
				disabledDel: true,
				currentPage: 1,
				totalPage: 0
			}
		},
		mounted() {
			this.getDocument();
		},
		methods: {
			// 获取资讯类型
			getDocument() {
				var self = this;
				API.documents(self.currentPage).then(res => {
					self.tableDate = res.data
					self.totalPage = res.total
				})
			},
			
			// 提交文档
			submit() {
				var self = this
				API.document(self.form).then(res => {
					self.$message.success('提交成功')
					self.dialogDoc = false
					self.getDocument();
					self.currentPage = 1
				})
			},
			handleDelete(index, row) {
				console.log(index, row);
			},
			// 删除多行
			delSelection(rows) {
				if (rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.delSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			// 富文本选择图片时的事件
			beforeUpload() {
				// 显示加载动画
				loading = this.$loading({
					lock: true,
					text: '图片上传中',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			// 富文本插入图片时上传图片
			quillImgSuccess(res, file) {
				console.log(res)
				let quill = this.$refs.myQuillEditor.quill
				// 关闭动画
				loading.close();
				if (res.data) {
					let length = quill.getSelection().index;
					quill.insertEmbed(length, 'image', res.data)
					quill.setSelection(length + 1)
				} else {
					this.$message.error('图片插入失败')
				}
			},
			// 选择表格行
			handleSelectionChange(val) {
				this.multipleSelection = val;
				this.disabledDel = false;

				if (this.multipleSelection == '') {
					this.disabledDel = true;
				}
			},
			// 分页
			handleCurrentChange(val) {
				var self = this;
				self.getDocument();
			},

		}
	}
</script>

<style scoped lang="scss">
	.editor-btn {
		margin-top: 30px;
	}
	
	.btn {
		margin-bottom: 1vw;
	}
	
	.el-table {
		border: 1px solid #dfe6ec;
	}
	
	.block {
		text-align: center;
		margin-top: 1vw;
	}
	
	.submit {
		margin-top: 1vw;
		text-align: right;
	}
</style>
